import { Component, OnInit, Injector, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ApartmentService } from '../../../../api/services/apartment.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';


@Component({
  selector: 'app-income-sub-ledger',
  templateUrl: './income-sub-ledger.component.html',
  styleUrls: ['./income-sub-ledger.component.scss']
})
export class IncomeSubLedgerComponent implements OnInit {
 
  isSubLedgerDataLoaded:boolean = false;

  subLedgerData:string = "";
  subLedgerDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "primayContact";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  totalDueAmount:number = 0;
  netDueAmount:number = 0;

  blockListData:any;

  filterSelected:string = "all";
  singleBlock:string = "Select Tower";
  selectedBlockId:number;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  dialogRef:any;

  selectedInput:string = "";
  columnField: any = {}

  @ViewChild("viewInvoiceTypeRef", { static: false }) viewInvoiceTypeRef: TemplateRef<any>;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { 
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){

    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;

  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getDueAmount(value){
  	return value != 'Due' ? true : false;
  }

  getSelected(type){
  	return this.filterSelected == type ? 'selected' : '';
  }

  clearFilter(){
  	this.filterSelected = 'all';
  	this.singleBlock = "Select Block";
  }


  showInvoiceTypes(ledger){

    var invoiceParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      apartmentBlockUnitId: ledger.apartmentBlockUnitId
    }

      this.accountsService.getCustInvoicesByApartmentBlockUnitId(invoiceParams).subscribe((res:any) => {
        
        if(res.length == 0){
          ledger.isInvoiceAvailable = false;
        }
        else {
          ledger.isInvoiceAvailable = true;
        }

        this.dialogRef = this.dialog.open(this.viewInvoiceTypeRef, {
          panelClass: 'material',
          disableClose: true,
          data: ledger
        });

      });

    

  }

  dialogClose(){
    this.dialogRef.close();
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  getAllBlockData(){

  	this.isSubLedgerDataLoaded = false;

  	this.filterSelected = 'all';
  	this.singleBlock = "Select Block";

  	this.accountsService.GetIncomeTrackerSubLedgersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  	this.subLedgerDataList = res;


		this.totalItems = this.subLedgerDataList.length;

		this.netDueAmount = 0;
  	this.totalDueAmount = 0;

		_.each(this.subLedgerDataList, (item, index) => {

			this.netDueAmount = this.netDueAmount + item.due;

			if(item.due >= 0)
			this.totalDueAmount = this.totalDueAmount + item.due;

		});

		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

		this.isSubLedgerDataLoaded = true;

  	})

  }

  getSingleBlock(block){
  	
  	this.filterSelected = 'single';
  	this.singleBlock = block.apartmentBlockNumber;
  	this.selectedBlockId = block.apartmentBlockId;


  	this.accountsService.GetIncomeTrackerSubLedgersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		this.subLedgerDataList = res.filter(item => {
  			return item.apartmentBlockNumber == this.singleBlock;
	  	});

	  	this.totalItems = this.subLedgerDataList.length;

  		if(this.totalItems>this.itemLimit){
  			this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
  		}

	  	this.netDueAmount = 0;
	  	this.totalDueAmount = 0;

	  	_.each(this.subLedgerDataList, (item, index) => {

  			this.netDueAmount = this.netDueAmount + item.due;

  			if(item.due >= 0)
  			this.totalDueAmount = this.totalDueAmount + item.due;

		  });

  	});

  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

  	this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      this.blockListData = res;

      this.getAllBlockData();

    });

  }

}
