import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-income-view-defaulters',
  templateUrl: './income-view-defaulters.component.html',
  styleUrls: ['./income-view-defaulters.component.scss']
})
export class IncomeViewDefaultersComponent implements OnInit {
  
  isDefaultersDataLoaded:boolean = false;

  defaultData:string = "";
  defaultDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "type";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  isDefaultSelected:boolean = false;
  selectAllDefaulters:boolean = false;

  sendMessage:string = null;

  selectedInput:string = "";
  columnField: any = {}

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }


  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getAllDefaulters(){

    if(this.selectAllDefaulters){
      _.each(this.defaultDataList, (item)=>{
        item.checked = true;
      })
      this.isDefaultSelected = true;
    }
    else{
      _.each(this.defaultDataList, (item)=>{
        item.checked = false;
      })
      this.isDefaultSelected = false;
    }

  }

  getSelectedAllDefaulters(unit) {
    var length=0;
    _.each(this.defaultDataList, (item)=>{
        if(item.checked){
          length++;
        }
     })
    if(length>0){
      this.isDefaultSelected = true;
    }
    else{
      this.isDefaultSelected = false;
    }

    if(length==0){
      this.selectAllDefaulters = false;
    }

    if(length == this.defaultDataList.length){
      this.selectAllDefaulters = true;
    }
    
  }

  sendEmail() {
    this.isDefaultersDataLoaded = true;
    _.each(this.defaultDataList, (item, index)=>{
        if(item.checked){
          
        }
     })
  }

  sendSMS() {
    this.isDefaultersDataLoaded = true;
    _.each(this.defaultDataList, (item, index)=>{
        if(item.checked){
          
        }
     })
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  @HostListener('window:resize', ['$event'])
    onResize(event){

      if(event.target.innerWidth <= 991)
      this.isMobile = true;
      else
      this.isMobile = false;

  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

  	this.accountsService.GetIncomeTrackerDefaulterByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		this.defaultDataList = res;

  		this.totalItems = this.defaultDataList.length;

  		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

		this.isDefaultersDataLoaded = true;

  	}, error => {
    	console.log(error);
    });

  }

}
