import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-income-view-invoice',
  templateUrl: './income-view-invoice.component.html',
  styleUrls: ['./income-view-invoice.component.scss']
})
export class IncomeViewInvoiceComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "custInvoiceId";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  glAccountIndicator:any = "Income";

  selectedInput:string = "";
  columnField: any = {}

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getUserName(invoice, id){

  	var data = _.filter( this.userDataList, function( item ) {
      if(item.userId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.invoiceDataList.map(obj=> {
        if(obj.custInvoiceId == invoice.custInvoiceId){
          obj.postedByName = data[0].firstName+' '+data[0].lastName;
        }
      });

      return data[0].firstName+' '+data[0].lastName;
    }

  }

  getAccountName(invoice, account){

    var accountDetails = {
      glaccountId: 0
    };

    if(account == undefined || account.length == 0){
      accountDetails.glaccountId = 0;
    }
    else {
      accountDetails.glaccountId = account[0].glaccountId;
    }

    var data = _.filter( this.glAccountListData, function( item ) {
      if(item.glaccountId === accountDetails.glaccountId)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.invoiceDataList.map(obj=> {
        if(obj.custInvoiceId == invoice.custInvoiceId){
          obj.glaccountName = data[0].glaccountName;
        }
      });

      return data[0].glaccountName;
    }

    return '';
  }

  getDueAmount(due, paid){
    return (due-paid) > 0 ? 'high' : 'low';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  getInvoiceDate(invoice, date){

   this.invoiceDataList.map(obj=> {
      if(obj.custInvoiceId == invoice.custInvoiceId){
        obj.postedOn = moment(date).format("DD/MM/YYYY");
      }
   });

    return moment(date).format("DD/MM/YYYY");
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  updateInvoiceItem(invoice){

    setTimeout(() => {

      this.isInvoiceDataLoaded = false;

      let details = {
          
          "custInvoiceId": invoice.custInvoiceId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "apartmentBlockUnitId": invoice.apartmentBlockUnitId,
          "custInvoiceAmount": parseInt(invoice.custInvoiceAmount),
          "custInvoiceDate": new Date().toISOString(),
          "dueDate": invoice.dueDate,
          "tax1": 0,
          "tax2": 0,
          "tax3": 0,
          "isTax": invoice.isTax,
          "isRecurCustInvoice": invoice.isRecurCustInvoice,
          "isEmailSent": false,
          "isSmssent": false,
          "custInvoiceStatusId": 1,
          "postedBy": parseInt(this.cookieService.get('userId')),
          "postedOn": new Date().toISOString(),
          "billToPay": "string",
          "comments": invoice.comments || "",
          "penaltyAmount": 0,
          "penaltyComment": "string",
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString(),
          "transReference1": "string",
          "transReference2": "string",
          "invoiceGLAccounts": [
            { 
              "invoiceGlaccountId": invoice.invoiceGLAccounts[0].invoiceGlaccountId, 
              "apartmentId": parseInt(this.cookieService.get('apartmentId')),
              "invoiceId": 0,
              "glaccountId": invoice.invoiceGLAccounts[0].glaccountId,
              "comments": "string",
              "isActive": true,
              "insertedBy": parseInt(this.cookieService.get('userId')),
              "insertedOn": new Date().toISOString(),
              "updatedBy": parseInt(this.cookieService.get('userId')),
              "updatedOn": new Date().toISOString(),
              "amount": invoice.custInvoiceAmount
            }
          ]
      }

      this.accountsService.updateCustInvoice(details).subscribe((res:any) => {

        if(res.message){
          this.isInvoiceDataLoaded = true;
          this.sharedService.setAlertMessage("Invoice updated successfully");
       }
       else {
          this.isInvoiceDataLoaded = true;
       }

      },
      error => {
          this.isInvoiceDataLoaded = true;
      });

    }, 1000);

  }

  ngOnInit() {


	var accountParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      groupId: 3
  }



  this.accountsService.getAllGLAccounts().subscribe((res:any) => {

      this.glAccountListData = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
      });

  });

    var params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      apartmentBlockUnitId: this.route.params['value'].id
    }

  	this.accountsService.getCustInvoicesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		this.invoiceDataList = res;
      
      _.each(this.invoiceDataList, item => {
        item.towerUnit = item.apartmentBlockNumber + ' ' + item.apartmentBlockUnitNumber;
      })

  		this.totalItems = this.invoiceDataList.length;

  		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
		}

		this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

			this.userDataList = res;
			this.isInvoiceDataLoaded = true;

		}, error => {

		});

    
	

  	})

  }

}
